import "./styles.css";
const myLinks = {
  Github: "https://github.com/mightyphoenix",
  Linkedin: "https://linkedin.com/in/mightyphoenix7",
  Twitter: "https://twitter.com/hsebinga",
  "Resume?": "#"
};
const links = document.querySelector("#links");

for (let key in myLinks) {
  const li = document.createElement("a");
  li.target = "_blank";
  li.innerText = key;
  li.href = myLinks[key];
  links.appendChild(li);
}
